import React, { useState } from 'react';
import _ from 'lodash';
import { Link, withRouter } from 'react-router-dom';
import s from './Main.module.css';
import classNames from 'classnames';
import { Location } from 'history';
import { useCity } from 'pages/Main/index';
import cities from 'data/cities';

// ToDo: remove any if adding elements
const navItems: any = [
  // {
  //   label: 'Истории успеха',
  //   to: '/success-stories',
  // },
  // {
  //   label: 'Видеоинтервью',
  //   to: '/interview',
  // },
];

type Props = {
  location: Location,
};

const Navigation: React.FC<Props> = ({ location }) => {
  const [showVacancyDropdown, setVacancyDropdown] = useState(false);
  const [stateCity] = useCity();

  const toggleDropdown = () => {
    setVacancyDropdown(!showVacancyDropdown);
  };

  const renderNavItems = () => {
    return _.map(navItems, (item) => {
      const { to, label } = item;
      if (!to) {
        return null;
      }

      const classes = classNames(s.navItem, {
        [s.navItemActive]: to === location.pathname,
      });
      return (
        <Link key={to} to={to} className={classes}>{label}</Link>
      );
    });
  };

  const vacancyClasses = classNames(s.navItem, {
    [s.navItemActive]: location.pathname.includes('/vacancy'),
  });

  const renderVacancyDropdown = () => {
    const cityVacancies = cities[stateCity.id].vacancies;
    if (!showVacancyDropdown || _.isEmpty(cityVacancies)) {
      return null;
    }

    const vacancyList = _.map(cityVacancies, (vacancy) => {
      return (
        <div key={vacancy.id} className={s.dropdownItem}>
          <Link to={`/vacancy/${vacancy.id}`} onClick={toggleDropdown} className={s.dropdownItemLink}>{vacancy.title}</Link>
        </div>
      );
    });

    return (
      <div className={s.navDropdown}>
        {vacancyList}
      </div>
    );
  }

  return (
    <div className={s.navItems}>
      <div className={vacancyClasses} onClick={toggleDropdown}>
        Вакансии
      </div>
      {renderNavItems()}
      {renderVacancyDropdown()}
    </div>
  );
};

export default withRouter(Navigation);
