import React, { ReactNode, useContext, useReducer } from 'react';
import Footer from './Footer';
import Header from './Header';
import routes from './routes';
import s from './Main.module.css';
import { CityKey } from 'data/cities';

interface CityReducerState {
  id: CityKey,
  name: string,
}

interface CityReducerAction {
  type: 'changeCity',
  id: CityKey,
  name: string,
}

type CityHook = [CityReducerState, React.Dispatch<CityReducerAction>];

const getInitialState = () => {
  const lsCity = localStorage.getItem('city');
  if (!lsCity) {
    return {
      id: 'moscow',
      name: 'Москва',
    };
  }

  const cityParsed = JSON.parse(lsCity);
  return {
    id: cityParsed.id,
    name: cityParsed.name,
  };
};
const reducer = (state: CityReducerState, action: CityReducerAction) => {
  if (action.type === 'changeCity') {
    const { id, name } = action;
    localStorage.setItem('city', JSON.stringify({ id, name }))
    return {
      id,
      name,
    };
  }

  return state;
};

const CityContext = React.createContext({});

const CityProvider: React.FC<{ children: ReactNode }> = (props) => {
  const contextValue = useReducer(reducer, getInitialState());
  return (
    <CityContext.Provider value={contextValue}>
      {props.children}
    </CityContext.Provider>
  );
}

export const useCity = () => (useContext(CityContext) as CityHook);

const Main: React.FC = () => {
  return (
    <CityProvider>
      <div className={s.main}>
        <Header />
        <div className={s.content}>
          {routes}
        </div>
        <Footer />
      </div>
    </CityProvider>
  );
};

export default Main;
