import React from 'react';
import s from './Vacancy.module.css';
import Features from 'ui/Sections/Features';
import Steps from 'ui/Sections/Steps';
import VacancyBlock from './VacancyBlock';
import cities, { CityKey } from 'data/cities';
import _ from 'lodash';

import offering from 'images/offering.jpg';
import requirements from 'images/requirements.jpg';
import vacancyGirl from 'images/vacancyGirl.png';

interface Props {
  id: string,
}

const Vacancy: React.FC<Props> = (props) => {
  const renderVacancyBlock = () => {
    const vacancies = _.flatMap((_.keys(cities) as CityKey[]), city => cities[city].vacancies);
    const vacancy = _.find(vacancies, v => v.id === props.id);
    if (!vacancy) {
      return null;
    }

    return (
      <VacancyBlock
        title={vacancy.title}
        desc={vacancy.desc}
        schedule={vacancy.schedule}
        salary={vacancy.salary}
        image={vacancy.banner}
        color={vacancy.color}
        position={vacancy.position}
      />
    );
  };

  return (
    <div className={s.header}>
      {renderVacancyBlock()}
      <Features />
      <div className={s.points}>
        <div className={s.title}>Твои задачи</div>
        <div className={s.pointsList}>
          <div className={s.sellPoint}>
            <div className={s.subTitle}>Продавать</div>
            <div className={s.pointDescription}>
              Продавать онлайн-продукты и услуги банка клиентам и услуги банка клиентам и отделении,
              используя планшет и терминалы самообслуживания
            </div>
          </div>
          <div className={s.helpPoint}>
            <div className={s.subTitle}>Помогать</div>
            <div className={s.pointDescription}>
              Выявлять цели визита клиента в банка и направлять его к соответствующим специалистам отделения
            </div>
          </div>
          <div className={s.suggestPoint}>
            <div className={s.subTitle}>Предлагать</div>
            <div className={s.pointDescription}>
              Объяснять клиентам преимущества использования онлайн-услуг, проводить обучение и презентации
            </div>
          </div>
        </div>
      </div>
      <Steps />
      <div className={s.listContainer}>
        <div className={s.listBlock}>
          <div className={s.listHeader}>
            <img width={'100%'} src={requirements} alt="requirements"/>
          </div>
          <div className={s.listContent}>
            <div className={s.listTitle}>
              Ты подходишь нам, если ты:
            </div>
            <ul className={s.list}>
              <li>специалист со средним профессиональным/неполным высшим образованием;</li>
              <li>владеешь программами Microsoft Office, Internet Explorer, Outlook;</li>
              <li>выдерживаешь высокий темп работы;</li>
              <li>готов работать в операционном зале в течение всего рабочего дня;</li>
              <li>доброжелательно относишься к окружающим, готов оказывать помощь;</li>
              <li>находишь общий язык с людьми разного возраста, пола и социального статуса;</li>
              <li>ориентирован на высокие результаты в продажах.</li>
            </ul>
          </div>
        </div>
        <div className={s.listBlock}>
          <div className={s.listHeader}>
            <div className={s.listHeader}>
              <img width={'100%'} src={offering} alt="offering"/>
            </div>
          </div>
          <div className={s.listContent}>
            <div className={s.listTitle}>
              Мы предлагаем:
            </div>
            <ul className={s.list}>
              <li>Трудоустройство согласно ТК РФ</li>
              <li>Удобный график работы</li>
              <li>Гарантированный доход плюс премиальное вознаграждение</li>
              <li>Возможность работать рядом с домом/местом учебы</li>
              <li>Регулярное корпоративное обучение</li>
              <li>ДМС, страхование от несчастных случаев и тяжелых заболеваний</li>
              <li>Материальную помощь и социальную поддержку, корпоративную пенсионную программу</li>
              <li>Льготные условия кредитования</li>
              <li>Яркую и насыщенную корпоративную жизнь</li>
            </ul>
          </div>
        </div>
      </div>
      <div className={s.form}>
        <img src={vacancyGirl} alt="vacancyGirl" className={s.vacancyGirl}/>
        <div className={s.formControl}>
          <div className={s.fromTitle}>
            Оставь заявку сейчас!
          </div>
          <div className={s.formDesc}>
            Все поля обязательны для заполнения
          </div>
          <div className={s.formBlock}>
            <input
              className={s.formInput}
              id="FirstName"
              type="text"
              placeholder={'Имя'}
            />
            <input
              className={s.formInput}
              id="LastName"
              type="text"
              placeholder={'Фамилия'}
            />
            <input
              className={s.formInput}
              id="EmailAddress"
              type="text"
              placeholder={'E-Main'}
            />
            <input
              className={s.formInput}
              id="MobilePhone"
              type="text"
              placeholder={'Номер телефона'}
            />
            <input
              className={s.formInput}
              id="BirthDate"
              type="date"
              placeholder={'Дата рождения'}
            />
            <button className={s.formSubmit} id="SkillazSubmit">Отправить заявку</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Vacancy;
