import React, { useEffect, useState } from 'react';
import s from './Main.module.css';
import locationIcon from 'images/location.svg';
import cities, { CityKey } from 'data/cities';
import _ from 'lodash';
import { useCity } from 'pages/Main/index';
import queryString from 'query-string';
import { Location } from 'history';
import { withRouter } from 'react-router';
import { history } from 'index';
import classNames from 'classnames';

const CityNavigation: React.FC<{ location: Location }> = (props) => {
  const [stateCity, setCity] = useCity();
  const [showLocationChange, setLocationChange] = useState(false);

  const changeCity = (id: CityKey, name: string) => {
    setCity({
      id,
      name,
      type: 'changeCity',
    });
  };

  useEffect(() => {
    const { search } = props.location;
    if (search) {
      const queries = queryString.parse(search);
      const queryCity = cities[(queries.city as CityKey)];
      if (queries.city && queryCity) {
        changeCity((queries.city as CityKey), queryCity.cityName);
      }

      const vacancyCity = _.find(_.keys(cities) as CityKey[], (c) => {
        const value = _.find(cities[c].vacancies, v => v.id === queries.vacancyId);
        return value ? c : undefined;
      });

      if (queries.vacancyId && vacancyCity) {
        changeCity((vacancyCity as CityKey), cities[(vacancyCity as CityKey)].cityName);
        history.push(`/vacancy/${queries.vacancyId}`);
      }
    }
  }, [props.location]);

  const handleCityChange = (id: CityKey, name: string) => () => {
    changeCity(id, name);
    setLocationChange(false);
  };

  const renderLocationChange = () => {
    if (!showLocationChange) {
      return null;
    }

    const cityOptions = _.map((_.keys(cities) as CityKey[]), city => ({ id: city, name: cities[city].cityName }));
    const cityList = _.map(cityOptions, (option) => {
      const { id, name } = option;
      const cityNameClasses = classNames(s.modalCityName, {
        [s.modalCityMain]: id === 'moscow' || id === 'spb',
      });
      return (
        <div
          key={id}
          onClick={handleCityChange(id, name)}
          className={s.modalCity}
        >
          <div className={s.modalCityIcon} />
          <div className={cityNameClasses}>
            {name}
          </div>
        </div>
      );
    });

    return (
      <div>
        <div className={s.changeLocationOverlay} onClick={toggleLocationChange} />
        <div className={s.changeLocationModal}>
          <div className={s.modalHeader}>
            <div className={s.modalTitle}>Выберите город</div>
            <div className={s.modalClose} onClick={toggleLocationChange} />
          </div>
          <div className={s.locationModalCities}>
            {cityList}
          </div>
        </div>
      </div>
    );
  };

  const toggleLocationChange = () => {
    setLocationChange(!showLocationChange);
  };

  return (
    <div className={s.headerLocation}>
      <img src={locationIcon} alt="city" />
      <div>
        <div className={s.headerCity}>{stateCity.name}</div>
        <div
          onClick={toggleLocationChange}
          className={s.changeLocationBtn}
        >
          (сменить)
        </div>
      </div>
      {renderLocationChange()}
    </div>
  );
};

export default withRouter(CityNavigation);
